import { useNavigate, useParams, Outlet, Link } from "react-router-dom";
import { useState, useContext } from "react";
import { useEffect } from "react";
import Badge from '@mui/material/Badge';

import API from "./API";





import noteContext from './NoteContext';
export default function CommodityDetails() {




    const [grade_array, setgrade_array] = useState([]);

    const { label } = useParams()

    const { grade } = useParams()

    const navigate = useNavigate();

    const a = useContext(noteContext);



    let token = a.token;


    const fetchData = async () => {









        const res = await API.get("commodity/" + label, {
            headers: {
                Accept: 'application/json'
            }
        });


        setgrade_array(res.data);

        if (grade === undefined) {

            res.data.forEach(element => {


                if (parseInt(element['deals_count']) > 0) {

                    


                    navigate("/commodity/" + label + "/" + element['grade_slug']);

                }

            });



        }



    };







    useEffect((label) => {

        if (a.login_status === null)
            navigate("/login")


        else {

            fetchData(label);


        }
    }, []);





    return (

        <div>



            <div className="saksh_title">
                <p className="text-center">Minimum order quantity is 10 tonnes</p>


                <Link className="  btn btn-outline-primary " to={'/'}>  Back</Link>

                <h2>  {label}   </h2>




            </div>




            < hr />


            <div className="flex-container">




                {grade_array.map((item, index) => (


                    <div key={index}>
                        {(item['grade_slug'] == grade) ? (
                            <Link key={index} className="btn btn-outline-success  active m-2" to={`${item['grade_slug']}`}>
                                <Badge badgeContent={item['deals_count']} color="primary">

                                    {item['grade']}
                                </Badge>
                            </Link>
                        ) : (
                            <Link key={index} className="btn btn-outline-primary  m-2" to={`${item['grade_slug']}`}>
                                <Badge badgeContent={item['deals_count']} color="primary">

                                    {item['grade']}
                                </Badge>
                            </Link>
                        )}
                    </div>




                ))
                }
            </div>



            <Outlet />


        </div>

    )
}